@charset "utf-8";
@import "reset.css";
@import "font.css";

:root {
	--background-color: #151414;
	--txt-color: #f0f0f0;
}

body::-webkit-scrollbar {
	display: none;
}

body,
html {
	font-family: "Noto Sans KR", sans-serif;
	font-size: 62.5%;
	-ms-overflow-style: none;
	background-color: var(--background-color);
	color: var(--txt-color);
}

img {
	width: 100%;
}

.loading {
	font-size: 6rem;
	text-align: center;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%);
}