.list {
  font-size: 62.5%;
}

.list:nth-child(2),
.list:nth-child(3) {
  margin: 6rem 0 2rem;
}

/* Title */
.title {
  display: flex;
  justify-content: space-between;
  margin: 2rem 0;
  font-size: 1.8rem;
}

.title h2 {
  font-weight: 500;
}

/* Button */
.nav button {
  width: 2.4rem;
  height: 2.4rem;
  border-radius: 0.5rem;
  cursor: pointer;
  border: 0;
}

.nav button:nth-child(2) {
  margin-left: 1.2rem;
}

.nav button svg {
  font-size: 2rem;
  vertical-align: middle;
}


/* Swiper Slide */

.list h3 {
  font-size: 1.4rem;
  margin: 1rem 0 0.24rem;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.star {
  font-size: 1.2rem;
  vertical-align: text-bottom;
  margin: 0.8rem 0.6rem 0 0;
}

.star svg {
  width: 1.6rem;
  height: 1.6rem;
}

/* Carousel */
.list .poster {
  position: relative;
  border-radius: 0.4rem;
  overflow: hidden;
}

.list .img {
  padding-top: 148%;
}

.list .poster:hover .overview {
  transition: all .8s;
  opacity: 1;
}

.list .poster .overview {
  opacity: 0;
}

.list .poster div.overview {
  font-size: 1.2rem;
  position: absolute;
  top: 0;
  width: 84%;
  height: 100%;
  text-align: justify;
  line-height: 1.4;
  background: #000;
  padding: 10% 8%;
}

.list .poster div.overview p {
  overflow: hidden;
}

/* More */
.list .poster button.overview {
  width: 50%;
  position: absolute;
  background: transparent;
  left: 25%;
  bottom: 12%;
  padding: 0.4rem;
  border-radius: 0.4rem;
  border: 0.1rem solid var(--txt-color);
  color: var(--txt-color);
  cursor: pointer;
}

.list .poster button:hover {
  background: rgba(255, 255, 255, 0.14);
}

@media screen and (min-width: 540px) {
  .list .poster div.overview {
    line-height: 1.3;
  }
}

@media screen and (min-width: 1024px) {
  .list .poster div.overview {
    padding: 12% 8%;
    line-height: 1.4;
  }
}

@media screen and (min-width: 1440px) {

  .list:nth-child(2),
  .list:nth-child(3) {
    margin: 7.8rem 0 2rem;
  }

  /* Title */
  .title {
    margin: 2.4rem 0;
    font-size: 2.8rem;
  }

  .list .poster div.overview {
    padding: 18% 8%;
    line-height: 1.4;
  }

  .list h3 {
    font-size: 1.8rem;
    margin: 1.4rem 0 0.24rem;
  }

  .star {
    vertical-align: text-bottom;
    margin: 0.8rem 0.8rem 0 0;
  }

  .star svg {
    width: 2rem;
    height: 2rem;
  }

  p,
  button {
    font-size: 1.6rem;
  }
}

@media screen and (min-width: 1800px) {

  .list:nth-child(2),
  .list:nth-child(3) {
    margin: 10rem 0 2rem;
  }

  /* Title */
  .title {
    margin: 2.4rem 0;
    font-size: 2.8rem;
  }

  .list h3 {
    font-size: 2.1rem;
    margin: 1.4rem 0 0.24rem;
  }

  .star {
    vertical-align: text-bottom;
    margin: 1.4rem 1rem 0 0;
  }

  .star svg {
    width: 2.4rem;
    height: 2.4rem;
  }

  p.star {
    font-size: 1.8rem;
  }

  .nav button {
    width: 3.2rem;
    height: 3.2rem;
    border-radius: 0.5rem;
    cursor: pointer;
    border: 0;
  }

  .nav button svg {
    font-size: 2.8rem;
    vertical-align: middle;
  }

  p,
  button {
    font-size: 2rem;
  }

  .list .poster button.overview {
    bottom: 14%;
  }
}