.main header {
  background: linear-gradient(to top, rgba(21, 20, 20, 0), 92%, rgb(21, 20, 20)), linear-gradient(rgba(21, 20, 20, 0.1), 0.2%, rgba(0, 0, 0, 0));
  width: 100%;
  height: 5.2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  z-index: 3;
}

.main header img {
  width: 8.2rem;
  margin: 0.2rem 0 0 1.6rem;
}

.main header img:hover {
  opacity: 1;
  animation: logo-ani 0.8s linear 1;
}

.main header input {
  display: none;
  width: 18rem;
  height: 2rem;
  margin-right: 1.6rem;
  text-align: center;
  font-size: 1.2rem;
  color: #333;
  padding-left: 0.8rem;
  border-radius: 0.2rem;
  box-shadow: 0 0.1rem 0.4rem #222;
}

.main header input+svg {
  font-size: 2rem;
  margin-right: 1.6rem;
}

.main article {
  padding: 1.8rem 1.6rem;
}

@keyframes logo-ani {
  25% {
    transform: rotate(2.2deg) scale(1.01);
  }

  50% {
    transform: rotate(0.4deg) scale(1);
  }

  75% {
    transform: rotate(-2.2deg) scale(1);
  }
}


@media screen and (min-width: 425px) {
  .main header input {
    display: inline-block;
  }

  .main header input+svg {
    display: none;
  }
}

@media screen and (min-width: 540px) {
  .slider h3 {
    font-size: 2.4rem;
  }
}

@media screen and (min-width: 768px) {
  .main header {
    padding-top: 0.6rem;
  }

  .main header img {
    width: 10.2rem;
  }
}

@media screen and (min-width: 1024px) {
  .main header {
    padding: 1.2rem 0 2rem;
  }

  .main header input {
    height: 2.4rem;
    margin-right: 2rem;
  }

  .main article {
    padding: 3.2rem 2rem;
  }
}

@media screen and (min-width: 1440px) {
  .main header img {
    margin: 0.2rem 0 0 2rem;
  }

  .main article {
    padding: 3.2rem 2rem;
  }
}

@media screen and (min-width: 1800px) {
  .main header {
    height: 6.8rem;
  }

  .main header img {
    width: 12rem;
    margin: 0.2rem 0 0 2.4rem;
  }

  .main header img:hover {
    opacity: 1;
    animation: logo-ani 0.8s linear 1;
  }

  .main header input {
    font-size: 1.6rem;
    width: 24rem;
    height: 3rem;
    margin-right: 2.4rem;
  }

  .main article {
    padding: 6.2rem 2.4rem;
  }
}