.container h4 {
  font-size: 1.6rem;
  margin: 2.8rem 0 1.4rem;
}

.cast {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 1rem;
}

.cast .actors {
  line-height: 1.2;
  text-align: center;
}

.cast .actors li:nth-child(1) img {
  border-radius: 0.4rem;
}

.cast .actors li:nth-child(2) {
  display: none;
}

.cast .actors li:nth-child(3) {
  display: none;
}


@media screen and (min-width: 375px) {
  .container h4 {
    font-size: 1.8rem;
    margin: 4rem 0 1.4rem;
  }
}

@media screen and (min-width: 425px) {
  .container h4 {
    margin: 4.8rem 0 1.4rem;
  }
}

@media screen and (min-width: 540px) {
  .container h4 {
    margin: 5.2rem 0 1.4rem;
  }

  .cast .actors li:nth-child(2) {
    display: block;
    font-size: 1.2rem;
    font-weight: 700;
    margin: 1.4rem 0 .4rem;
    height: 1.4rem;

    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
  }

  .cast .actors li:nth-child(3) {
    display: block;
    font-size: 1rem;
    height: 3rem;
  }
}

@media screen and (min-width: 768px) {
  .container h4 {
    font-size: 1.8rem;
    margin: 6.4rem 0 1.4rem;
  }

  .cast .actors li:nth-child(2) {
    height: 1.6rem;
  }

  .cast .actors li:nth-child(3) {
    font-size: 1rem;
    height: 3rem;
  }
}

@media screen and (min-width: 1024px) {
  .container h4 {
    font-size: 2.4rem;
  }

  .cast .actors li:nth-child(2) {
    font-size: 1.6rem;
    margin: 1.4rem 0 1.2rem;
    height: 2rem;
  }

  .cast .actors li:nth-child(3) {
    font-size: 1.4rem;
    height: 3rem;
  }
}

@media screen and (min-width: 1440px) {
  .cast .actors li:nth-child(2) {
    font-size: 2rem;
    margin: 1.4rem 0 1.6rem;
  }

  .cast .actors li:nth-child(3) {
    font-size: 1.8rem;
  }
}