header {
  background: var(--background-color);
  width: 100%;
  height: 3rem;
  padding-top: .4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header img {
  width: 5.2rem;
  margin: .2rem 0 0 .4rem;
}

header input {
  display: none;
}

header input+svg {
  font-size: 1.4rem;
  margin-right: .4rem;
}

@media screen and (min-width: 375px) {
  header {
    height: 3.6rem;
    padding-top: .4rem;
  }

  header img {
    width: 6.2rem;
    margin: .2rem 0 0 .4rem;
  }

  header input+svg {
    font-size: 1.6rem;
    margin-right: .4rem;
  }
}

@media screen and (min-width: 768px) {
  header img {
    width: 8.2rem;
  }

  header input {
    display: inline-block;
    width: 18rem;
    height: 2rem;
    margin-right: .4rem;
    text-align: center;
    font-size: 1.2rem;
    color: #333;
    padding-left: 0.8rem;
    border-radius: 0.2rem;
    box-shadow: 0 0.1rem 0.4rem #222;
  }

  header input+svg {
    display: none;
  }

  header {
    padding-top: 0.6rem;
  }
}

@media screen and (min-width: 1024px) {
  header {
    padding: 1.2rem 0 1rem;
  }

  header img {
    margin: .4rem 0 0 .4rem;
  }

  header input {
    height: 2.4rem;
  }
}

@media screen and (min-width: 1800px) {
  header {
    height: 4.2rem;
  }

  header img {
    width: 10rem;
  }

  header input {
    font-size: 1.6rem;
    width: 24rem;
    height: 3rem;
  }
}