.similar h4 {
	font-size: 1.6rem;
	margin: 5.2rem 0 1.4rem;
}

.posters {
	width: 100%;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-column-gap: 1rem;
	grid-row-gap: 2rem;
}

.posters img {
	height: 90%;
	border-radius: .4rem;
}

.posters h3 {
	text-align: center;
	margin-top: .4rem;
	font-size: 1.2rem;
	text-overflow: ellipsis;
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
}


@media screen and (min-width: 375px) {
	.similar h4 {
		font-size: 1.8rem;
		margin: 5.8rem 0 1.4rem;
	}

	.posters h3 {
		font-size: 1.4rem;
	}
}

@media screen and (min-width: 768px) {
	.posters {
		grid-template-columns: repeat(5, 1fr);
		grid-column-gap: .8rem;
		grid-row-gap: 0;
	}

	.similar h4 {
		font-size: 1.8rem;
		margin: 5.6rem 0 1.4rem;
	}

	.posters h3 {
		font-size: 1.2rem;
	}
}

@media screen and (min-width: 1024px) {
	.similar h4 {
		font-size: 2.4rem;
		margin: 9.2rem 0 1.4rem;
	}

	.posters h3 {
		font-size: 1.4rem;
	}
}

@media screen and (min-width: 1024px) {
	.posters h3 {
		font-size: 1.8rem;
	}
}